import { colors } from "styles/colors"
import { css } from "styled-components"

const background = css`
  .background-primary {
    background-color: ${({ theme }) => theme.background_primary} !important;
  }
  .background-secondary {
    background-color: ${({ theme }) => theme.background_secondary} !important;
  }
  .background-hight-contrast {
    background-color: ${({ theme }) => theme.hight_contrast} !important;
  }
  .background-gray {
    background-color: ${colors.root.gray} !important;
  }
`
export default background
