import { colors } from "styles/colors"
import { css } from "styled-components"
import Color from "color"

export const outlinedRedButton = css`
  color: ${({ theme }) => theme.hight_contrast_reverse};
  border: 1px solid ${({ theme }) => theme.background_tint_red};
  background-color: ${({ theme }) =>
    Color(theme.background_tint_red).fade(0.9)};
  :hover {
    color: ${colors.root.white};
    background-color: ${({ theme }) => theme.background_tint_red};
    svg {
      fill: ${colors.root.white} !important;
    }
  }
  svg {
    opacity: 0.5;
    fill: ${({ theme }) => theme.hight_contrast_reverse} !important;
  }
`

