import { styleVariables } from "projectSettings"
import { css } from "styled-components"

const border = css`
  .border-radius-primary {
    border-radius: ${styleVariables.borderRadius.primary};
  }
  .border-radius-secondary {
    border-radius: ${styleVariables.borderRadius.secondary};
  }
  .border-radius-tertiary {
    border-radius: ${styleVariables.borderRadius.tertiary};
  }
`

export default border
