import { css } from "styled-components"

const text = css`
  .text-primary {
    color: ${({ theme }) => theme.text_primary} !important;
  }
  .text-secondary {
    color: ${({ theme }) => theme.text_secondary} !important;
  }
  .text-tertiary {
    color: ${({ theme }) => theme.text_tertiary} !important;
  }
  .text-quarternary {
    color: ${({ theme }) => theme.text_quarternary} !important;
  }
  .text-tint-blue {
    color: ${({ theme }) => theme.foreground_tint_blue} !important;
  }
  .text-hight-contrast {
    color: ${({ theme }) => theme.hight_contrast} !important;
  }
`
export default text
