import styled, { css } from "styled-components"

export const Icon = styled.i`
  display: inline-flex;
  align-items: center;
  justify-content: center;
  svg {
    fill: gray;
  }

  // Disabled
  ${props => (props.disabled ? disabledButton : null)}
`
export const ClickArea = styled.div`
  position: absolute;
  cursor: pointer;
  display: flex;
  width: 36px;
  height: 36px;
`
const disabledButton = css`
  opacity: 0.6;
  pointer-events: none;
`
